/*******************************************************/
/*** basic elements and bootstrap overrides ***/
/*******************************************************/


html {
	text-rendering: optimizelegibility;
	position: relative;
	min-height: 100%;
}

html,body {
	width: 100%;
	margin: 0px;
	padding: 0px;
}

body {
	-webkit-overflow-scrolling: touch;
	margin-bottom:50px;
}

#pagewrapper {
	position:relative;
}

body {
	font-family: "Lato", "Helvetica", sans-serif;
	font-weight:300;
	text-rendering: optimizelegibility;
}

h1,h2,h3,h4,h5 {
	font-weight: 300;
	font-family: "Ubuntu", sans-serif;
	letter-spacing:.025em;
}

h2, h3, h4, h5, h6, .h2, .h3, .h4, .h5, .h6{
	line-height:1.1;
}

h1 {
	font-size: 3em;
	color: #333;
	font-weight: 400;
	line-spacing:1;
	text-shadow:0 1px 2px rgba(0, 0, 0, 0.3);
}

h2 {
	font-size: 2.2em;
}

h3 {
	font-size: 1.75em;
}

p {
	color: #666666;
}

a, a:visited {
	color: #2980b9;
}

a:hover, a:focus{
	color:#333;
}

.feature .title {
	font-weight: 500 !important;
	margin: 0;
	line-height: 1;
}

h3 {
	font-weight: 300;
	font-family:  sans-serif;
	letter-spacing:.025em;
	text-transform:uppercase;
	line-spacing:1.5;
}

h2 {
	color:#666;
}

h3, h4, h5 {
	color:#444;
}

hr {
	margin:20px auto;
}

.clearfix {
	*zoom: 1;
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
	line-height: 0;
}
.clearfix:after {
	clear: both;
}


.header {
	padding-top: 45px;
}

.header {
	background: rgba(0, 0, 0, 1);
	box-shadow: inset rgba(255, 255, 255, 0.3) 0 1px 0, inset rgba(0, 0, 0, 0.22) 0 -1px 0, rgba(0, 0, 0, 0.14) 0 1px 2px;
	width: 100%;
	height: 65px;
	text-align: center;
	padding-top: 25px;
}

.header .logo {
	height:20px;
	margin:-15px auto 0;
}

.logo-display {
	text-align: center;
	position: relative;
	margin-right: auto;
	margin-left: auto;
}

.logo-display img.logo {
	width: 350px;
	padding: 30px 0 20px 0;
}

.col-centered {
	float: none;
	display:inline-block;
	margin: 0 auto;
}

/******************* Navbar *******************/

#nav {
	border:none;
}

#nav.navbar, #nav.navbarfixed {
	border-bottom: 1px solid #666;
	border-left: none;
	border-right: none;
	border-top: none;
	border-radius: 0;
	background-color:#000;
	font-weight:400;
	letter-spacing:.025em;
	box-shadow:0 1px 4px rgba(0, 0, 0, 0.1);
	margin-bottom: 0 !important;
}

#nav.affix {
	position: fixed;
	top: 0;
	width: 100%;
	z-index:500;
}

.navbar {
	margin-bottom: 0 !important;
	min-height:0;
	i {
		padding: 0 !important;
		color:#fff;
	}
}
.navbar-icons ul {
	margin:18px 0 18px 250px;
}
.navbar-icons ul li {
	list-style:none outside none;
	margin-left:15px;
