	color:#FFF !important;
}
.navbarfixed {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	width: 100%;
	z-index:500;
}

.navbar-inner {
	display:table;
	margin: 0 auto;
}
.navbar-collapse {
