}

.nav > li > a:hover, .nav > li > a:focus {
	background-color:#2c3e50;
	outline:none;
}

.nav > li > a.active {
	font-weight:bold;
}

.nav > li > a > img{
	height:14px;
}

.navbar-brand{
	padding-top:16px;
	line-height:16px;
	outline:none;
