/*******************************************************/
/*** section styles ***/
/*******************************************************/

#landing {
	#content {
		h1, h2, h3, h4, p {
			text-align: center;
		}
		h3, h4 {
			color:#444;
		}
		.content-container {
			section:nth-child(1) {
				padding: 60px 0;
				section:first-of-type {
					margin-bottom: 60px;
				}
				section:nth-child(2) {
					margin-top:-60px;
					margin-bottom: 60px;
				}
			}
			section > section:last-of-type {
				margin-bottom:-60px;
			}
		}
	}
}


.container-fluid.wrapper section  {
	width: calc(100% + 60px) !important;
	margin-left: -30px;
	margin-right: -30px;
	padding:0;
}


.container-fluid.wrapper section.full-width {
	width: calc(100% + 60px) !important;
	padding:0 !important;
	.container {
		width: 100% !important;
		padding: 0 !important;
		margin: 0 !important;
	}
}


section#hero:first-of-type {padding-top:5%;}

section {
